  --heading-lg-font-weight-sm: 400;
  --heading-lg-font-family: var(--font-family-name);
  --heading-md-font-size: clamp(1.5rem, 5vw, 1.75rem);
  --heading-md-line-height: clamp(2rem, 5vw, 2.25rem);
