  --quote-font-size: 1.5rem;
  --quote-line-height: 2.25rem;
  --quote-font-weight: 400;
  --quote-font-family: var(--font-family-name);
  /* placeholder */
  --placeholder-font-size: 1rem;
  --placeholder-line-height: 1.5rem;
  --placeholder-font-weight: 400;
  --placeholder-font-family: var(--font-family-name);
  /* caption */
  --caption-font-size: 0.875rem;
  --caption-line-height: 1.375rem;
  --caption-font-weight: 400;
  --caption-font-family: var(--font-family-name);
  /* button-label-lg */
  --button-label-lg-font-size: 1.25rem;
  --button-label-lg-line-height: 1.75rem;
  --button-label-lg-font-weight: 700;
  --button-label-lg-font-family: var(--font-family-name);
  /* button-label-md */
  --button-label-md-font-size: 1rem;
  --button-label-md-line-height: 1.5rem;
  --button-label-md-font-weight: 700;
  --button-label-md-font-family: var(--font-family-name);
  /* button-label-sm */
  --button-label-sm-font-size: 0.875rem;
  --button-label-sm-line-height: 1.375rem;
  --button-label-sm-font-weight: 700;
  --button-label-sm-font-family: var(--font-family-name);
  /* form-label */
  --form-label-font-size: 1rem;
  --form-label-line-height: 1.5rem;
  --form-label-font-weight: 700;
  --form-label-font-family: var(--font-family-name);
  /* date-label-md */
  --date-label-md-font-size: 1rem;
  --date-label-md-line-height: 1.5rem;
  --date-label-md-font-weight: 400;
  --date-label-md-font-family: var(--font-family-name);
  /* date-label-sm */
  --date-label-sm-font-size: 0.875rem;
  --date-label-sm-line-height: 1.375rem;
  --date-label-sm-font-weight: 400;
  --date-label-sm-font-family: var(--font-family-name);
  /* label-lg */
  --label-lg-font-size: 1.25rem;
  --label-lg-line-height: 1.75rem;
  --label-lg-font-weight: 700;
  --label-lg-font-family: var(--font-family-name);
  /* label-md */
  --label-md-font-size: 1rem;
  --label-md-line-height: 1.5rem;
  --label-md-font-weight: 700;
  --label-md-font-family: var(--font-family-name);
  /* label-sm */
  --label-sm-font-size: 0.875rem;
  --label-sm-line-height: 1.375rem;
  --label-sm-font-weight: 700;
  --label-sm-font-family: var(--font-family-name);
