/* THEME BASELINE STYLES
------------------------------------------------------------------------------*/
body {
  @apply text-body-md text-[--text-content] bg-[--bg-body];
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--wp--preset--font-family--system-sans-serif);
}

a:where(:not(.wp-element-button)) {
  @apply text-[--interactive] underline hover:no-underline active:underline;
}

.aligncenter {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

@media (min-width: 1536px) {
  .container {
    max-width: var(--wp--style--global--content-size) !important;
  }
}
@media (min-width: var(--wp--style--global--content-size)) {
