/* COMPONENTS STYLES
------------------------------------------------------------------------------*/
.accordion-item {
  @apply hover:cursor-pointer text-body-md p-[--space-150] border-[--border-light] mb-2;
}
.component-sidebar-contextual-nav__accordion .accordion-item {
  @apply p-[--space-100] mb-0;
}
.accordion-item:last-child {
  @apply mb-0;
}
.accordion-item__body {
  @apply mt-[--space-250] pr-12;
}
.component-sidebar-contextual-nav__accordion .accordion-item__body {
  @apply mt-[--space-100];
}
.accordion-item__body ul li {
  @apply flex gap-2;
}
.accordion-item__body ul li:before {
  @apply min-w-4;
  content: "•";
}
.accordion-item__body ol {
  counter-reset: list-item;
}
.accordion-item__body ol li {
  @apply flex gap-2;
  counter-increment: list-item;
}
.accordion-item__body ol li:before {
  @apply min-w-4;
  content: counter(list-item) ".";
}
.accordion-item__title {
  @apply text-body-md;
}
.accordion-item__title a {
  @apply text-[--text-content] no-underline;
}
.component-sidebar-contextual-nav__accordion .accordion-item__title a {
  @apply text-[--nav-contextual-link-lvl-1] font-bold hover:underline;
}
.accordion-item__icon {
  @apply ml-4 text-[--interactive];
}
.open .accordion-item__icon {
  @apply text-[--highlight-dark];
}
.accordion-item.open {
  @apply border-[--highlight-dark] bg-[--highlight-light];
}
.component-sidebar-contextual-nav__accordion .accordion-item.open {
  @apply bg-transparent border-[--border-light];
}
.accordion-item--list {
  @apply border-t-[length:--border-sm] border-b-[length:--border-sm];
}
.component-sidebar-contextual-nav__accordion .accordion-item--list {
  @apply border-t-0;
}
.component-sidebar-contextual-nav__accordion .accordion-item--list:last-child {
  @apply border-b-0;
}
.accordion-item--list.open {
  @apply border-b-[length:--border-lg];
}
.component-sidebar-contextual-nav__accordion .accordion-item--list.open {
  @apply border-b-[length:--border-sm];
}
.accordion-item--card {
  @apply border-[length:--border-sm] rounded-[--interactive-corner-radius-md];
}

.component-author {
  @apply flex flex-col md:flex-row gap-[--space-300];
}
.component-author__avatar {
  @apply flex-[0_1_72px] min-w-[72px] md:flex-[0_1_120px] md:min-w-[120px];
}
.component-author__avatar img {
  @apply max-w-[72px] md:max-w-[120px] rounded-[--image-corner-radius];
}
.component-author__name {
  @apply text-heading-xs;
  @apply font-bold;
}
.component-author__info {
  @apply flex flex-col gap-[--space-300];
}
.component-author__bio {
  @apply text-body-md;
}

.component-banner {
  @apply py-8 px-4 relative rounded-[--banner-radius] overflow-hidden;
}
.component-banner__post-type {
  @apply text-label-md;
}
.component-banner__title {
  @apply text-heading-3xl;
}
.component-banner__intro {
  @apply text-body-lg;
}
.component-banner__date {
  @apply text-date-label-md;
}
.component-banner .cta-card {
  @apply flex-1 p-[--space-300] basis-full w-full lg:basis-3/12 rounded-[--card-accent-corner-radius];
}
.component-banner .cta-card__title {
  @apply text-heading-sm mb-[--space-250];
}
.component-banner .cta-card__body {
  @apply mb-[--space-300] text-body-md;
}
.component-banner .cta-card__links {
  @apply flex flex-col gap-[--space-150];
}
.component-banner__hr {
  @apply border-t-[length:--border-lg] w-[54px] m-auto;
