.woocommerce-info,
.woocommerce-message,
.woocommerce-error {
  @include wc-alert;

  @media (max-width: 767px) {
    margin-inline: var(--space-200);
  }
}

.woocommerce-info {
  @apply bg-[--state-info-primary-light] border-[--state-info-primary];

  &::before {
    background-image: url("data:image/svg+xml;base64,");
  }
}

.woocommerce-error {
  @apply bg-[--state-error-light] border-[--state-error];

  &::before {
    background-image: url("data:image/svg+xml;base64,");
  }
}

.woocommerce-notice {
  font-size: var(--heading-sm-font-size);
}
