// Temporary add typography variables
:root {
  --heading-3xl-font-size: clamp(2.5rem, 5vw, 3.25rem);
  --heading-3xl-line-height: clamp(3rem, 5vw, 3.75rem);
  --heading-3xl-font-weight-md: 700;
  --heading-3xl-font-weight-sm: 700;
  --heading-3xl-font-family: var(--font-family-name);

  --heading-2xl-font-size: clamp(2.25rem, 5vw, 2.75rem);
  --heading-2xl-line-height: clamp(2.75rem, 5vw, 3.25rem);
  --heading-2xl-font-weight-md: 400;
  --heading-2xl-font-weight-sm: 400;
  --heading-2xl-font-family: var(--font-family-name);

  --heading-xl-font-size: clamp(2rem, 5vw, 2.25rem);
  --heading-xl-line-height: clamp(2.5rem, 5vw, 2.75rem);
  --heading-xl-font-weight-md: 700;
  --heading-xl-font-weight-sm: 700;
  --heading-xl-font-family: var(--font-family-name);

  --heading-lg-font-size: clamp(1.75rem, 5vw, 2rem);
  --heading-lg-line-height: clamp(2.25rem, 5vw, 2.5rem);
  --heading-lg-font-weight-md: 400;
  --heading-lg-font-weight-sm: 400;
  --heading-lg-font-family: var(--font-family-name);

  --heading-md-font-size: clamp(1.5rem, 5vw, 1.75rem);
  --heading-md-line-height: clamp(2rem, 5vw, 2.25rem);
  --heading-md-font-weight-md: 700;
  --heading-md-font-weight-sm: 700;
  --heading-md-font-family: var(--font-family-name);

  --heading-sm-font-size: clamp(1.25rem, 5vw, 1.5rem);
  --heading-sm-line-height: clamp(1.75rem, 5vw, 2rem);
  --heading-sm-font-weight-md: 700;
  --heading-sm-font-weight-sm: 700;
  --heading-sm-font-family: var(--font-family-name);

  --heading-xs-font-size: clamp(1.125rem, 5vw, 1.25rem);
  --heading-xs-line-height: clamp(1.625rem, 5vw, 1.75rem);
  --heading-xs-font-weight-md: 700;
  --heading-xs-font-weight-sm: 700;
  --heading-xs-font-family: var(--font-family-name);

  /* body-lg */
  --body-lg-font-size: 1.25rem;
  --body-lg-line-height: 1.75rem;
  --body-lg-font-weight: 400;
  --body-lg-font-family: var(--font-family-name);

  /* body-md */
  --body-md-font-size: 1rem;
  --body-md-line-height: 1.5rem;
  --body-md-font-weight: 400;
  --body-md-font-family: var(--font-family-name);

  /* body-sm */
  --body-sm-font-size: 0.875rem;
  --body-sm-line-height: 1.375rem;
  --body-sm-font-weight: 400;
  --body-sm-font-family: var(--font-family-name);

  /* quote */
  --quote-font-size: 1.5rem;
  --quote-line-height: 2.25rem;
  --quote-font-weight: 400;
  --quote-font-family: var(--font-family-name);

  /* placeholder */
  --placeholder-font-size: 1rem;
  --placeholder-line-height: 1.5rem;
  --placeholder-font-weight: 400;
  --placeholder-font-family: var(--font-family-name);

  /* caption */
  --caption-font-size: 0.875rem;
  --caption-line-height: 1.375rem;
  --caption-font-weight: 400;
  --caption-font-family: var(--font-family-name);

  /* button-label-lg */
  --button-label-lg-font-size: 1.25rem;
  --button-label-lg-line-height: 1.75rem;
  --button-label-lg-font-weight: 700;
  --button-label-lg-font-family: var(--font-family-name);

  /* button-label-md */
  --button-label-md-font-size: 1rem;
  --button-label-md-line-height: 1.5rem;
  --button-label-md-font-weight: 700;
  --button-label-md-font-family: var(--font-family-name);

  /* button-label-sm */
  --button-label-sm-font-size: 0.875rem;
  --button-label-sm-line-height: 1.375rem;
  --button-label-sm-font-weight: 700;
  --button-label-sm-font-family: var(--font-family-name);

  /* form-label */
  --form-label-font-size: 1rem;
  --form-label-line-height: 1.5rem;
  --form-label-font-weight: 700;
  --form-label-font-family: var(--font-family-name);

  /* date-label-md */
  --date-label-md-font-size: 1rem;
  --date-label-md-line-height: 1.5rem;
  --date-label-md-font-weight: 400;
  --date-label-md-font-family: var(--font-family-name);

  /* date-label-sm */
  --date-label-sm-font-size: 0.875rem;
  --date-label-sm-line-height: 1.375rem;
  --date-label-sm-font-weight: 400;
  --date-label-sm-font-family: var(--font-family-name);

  /* label-lg */
  --label-lg-font-size: 1.25rem;
  --label-lg-line-height: 1.75rem;
  --label-lg-font-weight: 700;
  --label-lg-font-family: var(--font-family-name);

  /* label-md */
  --label-md-font-size: 1rem;
  --label-md-line-height: 1.5rem;
  --label-md-font-weight: 700;
  --label-md-font-family: var(--font-family-name);

  /* label-sm */
  --label-sm-font-size: 0.875rem;
  --label-sm-line-height: 1.375rem;
  --label-sm-font-weight: 700;
  --label-sm-font-family: var(--font-family-name);
}

//TODO: Temporary extend typography here

// Typography are defined without font-size because they are already defined via theme.json
.text-heading-3xl {
  @apply leading-[--heading-3xl-line-height] font-[--heading-3xl-font-weight-sm] md:font-[--heading-3xl-font-weight-md] font-[family-name:var(--heading-3xl-font-family)];
}

.text-heading-2xl {
